document.addEventListener('DOMContentLoaded', () => {
  const item = document.querySelectorAll(".faq__item")
  item.forEach(el => {
    el.addEventListener('click', (e) => {
      const self = e.currentTarget;
      const btn = self.querySelector(".faq__btn")
      const text = self.querySelector(".faq__text")
      const title = self.querySelector(".faq__subtitle")
      text.classList.toggle('faq__text--active')
      self.classList.toggle('faq__item--active')
      btn.classList.toggle('faq__btn--active')
      title.classList.toggle('faq__subtitle--active')

      if (text.classList.contains('faq__text--active')) {
        text.style.maxHeight = text.scrollHeight + 'px';
      } else {
        text.style.maxHeight = null;
      }

    });
  });



});


document.addEventListener('DOMContentLoaded', () => {
  const item = document.querySelectorAll(".program__inner")
  item.forEach(el => {
    el.addEventListener('click', (e) => {
      const self = e.currentTarget;
      const btn = self.querySelector(".program__acardion")
      const text = self.querySelector(".program__list")
      const title = self.querySelector(".program__subtitle")
      text.classList.toggle('program__list--active')
      self.classList.toggle('program__inner--active')
      btn.classList.toggle('program__acardion--active')
      title.classList.toggle('program__subtitle--active')

      if (text.classList.contains('program__list--active')) {
        text.style.maxHeight = text.scrollHeight + 'px';
      } else {
        text.style.maxHeight = null;
      }

      if (btn.classList.contains('program__acardion--active')) {
        btn.textContent = 'Скрыть';
      } else {
        btn.textContent = 'Подробности';
      }

    });
  });



});
