const form = document.querySelector(".form__form")
const form2 = document.querySelector(".form__form-1")
const form3 = document.querySelector(".graph-modal__form")
const form4 = document.querySelector(".graph-modal__form-two")
const telSelector = form.querySelector('input[type="tel"]')
const telSelector2 = form2.querySelector('input[type="tel"]')
const telSelector3 = form3.querySelector('input[type="tel"]')
const telSelector4 = form4.querySelector('input[type="tel"]')
const inputMask = new Inputmask('+7 (999) 999-99-99')
inputMask.mask(telSelector)
inputMask.mask(telSelector2)
inputMask.mask(telSelector3)
inputMask.mask(telSelector4)

new window.JustValidate('.form__form', {
  rules: {
    tel: {
      required: true,
    },

    radio: {
      required: true,
    }



  },
  messages: {
    tel: {
      required: 'Введите телефон',
    },

    radio: {
      required: ' ',
    },
  },
  submitHandler: function (thisForm) {
    let formData = new FormData(thisForm);
    let xhr = new XMLHttpRequest();

    xhr.onreadystatechange = function () {
      if (xhr.readyState === 4) {
        if (xhr.status === 200) {
          console.log('Отправлено');
        }
      }
    }
    xhr.open('POST', 'mail.php', true);
    xhr.send(formData);
    thisForm.reset();
  }
})




