new window.JustValidate('.graph-modal__form', {
  rules: {
    tel: {
      required: true,
    },

    radio: {
      required: true,
    }



  },
  messages: {
    tel: {
      required: 'Введите телефон',
    },

    radio: {
      required: ' ',
    },
  },
  submitHandler: function (thisForm) {
    let formData = new FormData(thisForm);
    let xhr = new XMLHttpRequest();

    xhr.onreadystatechange = function () {
      if (xhr.readyState === 4) {
        if (xhr.status === 200) {
          console.log('Отправлено');
          document.querySelector('.graph-modal__form').classList.add('graph-modal__form--active')
          document.querySelector('.graph-modal__complit').classList.add('graph-modal__complit--active')
          document.querySelector('.graph-modal__subtitle').classList.add('graph-modal__subtitle--active')

        }
      }
    }
    xhr.open('POST', 'mail.php', true);
    xhr.send(formData);
    thisForm.reset();

  }
})


